export default  {
    /**
     * 5211
     * @param cb
     */
    getTimeSpan(time){
        let now = new Date();
        time = new Date(time);
        time = time.getTime();
        let seconds = (now.getTime() - time) / 1000;
        if( seconds < 60){
            return parseInt(seconds) + ' ';
        }else if( seconds < 3600){
            return parseInt(seconds / 60) + ' ';
        }else if( seconds < 86400){
            return parseInt(seconds / 3660) + ' ';
        }else if( seconds < 2592000){
            return parseInt( seconds / 86400) + ' ';
        }else if( seconds < 31536000){
            return parseInt( seconds / 2592000) + ' ';
        }else{
            return parseInt( seconds / 31536000) + ' ';
        }
    }
}


